import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);//vue

//Vuex.Store 
const store = new Vuex.Store({
    state:{//
		"baseUrl":'http://localhost:5555',
        "userInfo":{},
    },
	mutations:{
		getUserInfo(state,userInfo){
			state.userInfo=userInfo;
		}
	}
})
export default store